/*! purgecss start ignore */
*,
::before,
::after {
  box-sizing: border-box;
  background-repeat: no-repeat;
}

/*stylelint-disable*/
html{
  box-sizing: border-box;
  overflow-x: hidden;
}

*, *::after, *::before{
  box-sizing: inherit;
}
.body{
  min-height: 100vh;
  position: relative;
  margin: 0;
}
.wrapper{
  margin: 0 auto;
  max-width: var(--max-width-conteiner);
  min-width: var(--min-width-conteiner);
}
.full-width:after{
  content:'';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

