html.open{
  overflow: hidden;
}

/*Header*/
.upper-header{
  background-color: #f2f2f2;
  font-size: 11px;
  font-family: var(--lato);
  position: relative;
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: auto 1fr auto;
  padding: 1.18em 0;
}

.upper-header__phone-link{
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
}
.upper-header__phone-icon{
  display: inline-block;
  width: 0.9em;
  height: 0.9em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  margin-right: 1.82em;
}

.upper-header__menu{
  grid-column-start: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0 0.73em 0 0;
}
.upper-header__menu-item{
  padding: 0 3em;
  margin: 0;
  border-right: 1px solid black;
}

.upper-header__menu-item:last-child{
  border-right: none;
  padding-right: 0;
}
.upper-header__menu-link{
  text-decoration: none;
  text-transform: uppercase;
  color: inherit;
}
@media screen and (max-width: 800px) {
  .upper-header{
    font-size: calc(100vw / 65);
  }
  .upper-header__menu{
    padding: 0;
  }
}
@media screen and (max-width: 600px) {
  .upper-header{
    display: block;
    font-size: calc(100vw / 42);
  }
  .upper-header__phone-link{
    display: none;
  }
  .upper-header__menu-item{
    padding: 0;
    border: none;
  }
}
@media screen and (max-width: 360px) {
  .upper-header{
    font-size: 9px;
  }
}

.lower-header{
  display: grid;
  grid-template-columns: auto 5fr auto 4fr auto 1fr auto auto;
  align-items: center;
  font-size: 12px;
  padding: 29px 0 18px;
}
@media screen and (max-width: 1330px) {
  .upper-header,
  .lower-header{
    padding-right: 20px;
    padding-left: 20px;
  }
}
.page-nav{
  position: relative;
}
.page-nav__menu{
  display: grid;
  grid-template-columns: 9fr 11fr 16fr 9fr;
  grid-column-gap: 1.83em;
  margin: -25px 0 0 0;
  padding: 0;
  list-style: none;
  transition: left 300ms;

}

.page-nav__link{
  text-decoration: none;
  color: inherit;
  font-family: var(--playfair);
  font-size: 18px;
  font-style: italic;
}
.lower-header__hamburger-button{
  display: none;
  border: none;
  background-color: unset;
  padding: 0;
  border: 0;
  width: 23px;
  height: 21px;
  background-repeat: no-repeat;
  background-image: url("../img/sprite.svg#hamburger");
}

.lower-header__logo-link{
  grid-column-start: 3;
  justify-self: center;
}
.lower-header__logo-img{
  width: 230px;
  height: 75px;
  margin: 2px 0 0 -8px;
}

.lower-header__social-menu{
  grid-column-start: 5;
  list-style: none;
  margin: -19px 0 0 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0 14px;
}

.lower-header__social-link{
  width: 34px;
  height: 34px;
  border: 2px solid;
  border-radius: 50%;
  color: var(--secondary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}
.lower-header__social-img{
  height: 14px;
  fill: currentColor;
}
.lower-header__select-wrapper{
  grid-column-start: 7;
  margin: -52px 6px 0 0;
  position: relative;
  width: 90px;
}
.lower-header__select-menu{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.lower-header__select-item{
  font-size: 12px;
  font-family: var(--lato);
  font-weight: bold;
  color: white;
  height: 35px;
  padding: 10px;
  width: 100%;
  text-transform: uppercase;
  text-decoration: none;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.lower-header__select-item:not(:first-child){
  display: none;
  border-top: 1px solid #404040;
}
.lower-header__select-item.open:not(:first-child){
  display: block;
}
.lower-header__select-icon{
  display: inline-block;
  width: 0.9em;
  height: 0.9em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
.lower-header__select-option{
  padding: 10px;
  margin: 10px 0;
}
@media screen and (max-width: 1290px) {
  .lower-header{
    font-size: calc(100vw / 107.5);
  }
}
@media screen and (max-width: 900px) {
  .page-nav__menu{
    margin: 0;
  }
  .lower-header__logo-img{
    height: 65px;
    margin: 0;
  }
  .lower-header__social-menu{
    margin: 0;
  }
  .lower-header{
    grid-template-columns: auto 1fr auto 20px auto;
    grid-template-rows: auto auto;
    grid-gap: 20px 0;
  }
  .lower-header__logo-link{
    grid-column-start: span 5;
    grid-row-start: 1;
  }
  .page-nav{
    grid-column-start: 1;
    grid-row-start: 2;
  }
  .lower-header__social-menu{
    grid-column-start: 3;
    grid-row-start: 2;
  }
  .lower-header__select-wrapper{
    grid-column-start: 5;
    grid-row-start: 2;
    margin: -35px 0 0 0;
  }
}
@media screen and (max-width: 600px) {
  .upper-header{
    display: block;
  }
  .lower-header{
    grid-template-columns: auto 1fr auto 1fr auto;
    grid-template-rows: auto auto;
    grid-gap: 20px 0;
  }
  .lower-header__hamburger-button{
    display: block;
  }
  .page-nav__menu{
    display: none;
    top: 0;
    position: fixed;
    left: 0;
    background-color: white;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    padding: 75px 20px;
  }
  .page-nav__item{
    padding: 20px 0;
    text-align: center;
    border-bottom: 1px solid lightgrey;
    margin-left: 30vw;
  }
  .lower-header__logo-img{
    height: 55px;
  }
  .lower-header__social-menu {
    grid-column-gap: 8px;
  }
}

.page-nav__menu.open{
  display: block;

}
.lower-header__hamburger-button.open{
  position: relative;
  background-image: url(../img/sprite.svg#close);
  z-index: 1001;
}


/*Banner*/
.banner-section:after{
  content: '';
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 200px solid transparent;
  border-right: 200px solid transparent;
  border-top: 60px solid white;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 1000;
}
@media screen and (max-width: 1290px) {
  .banner-section:after{
    border-left: calc(100vw / 6.45) solid transparent;
    border-right: calc(100vw / 6.45) solid transparent;
    border-top: calc(100vw / 21.5) solid white;
  }
}
@media screen and (max-width: 850px) {
  .banner-section:after{
    display: none;
  }
}
.banner-section__item{
  position: relative;
  font-family: var(--lato);
  font-weight: bold;
  text-transform: uppercase;
}
.banner-section__img{
  width: 100%;
}
.banner-section__info-block{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 259px;
  height: 259px;
  border: 1px solid white;
  padding: 2% 0;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
}

.banner-section__info-block:before{
  content: '';
  width: 107%;
  height: 107%;
  border: 2px solid white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.banner-section__info-text{
  margin: 0;
}
.banner-section__info-text_one{
  font-size: 1em;
  color: white;
}
.banner-section__info-text_two{
  font-size: 3em;
}
.banner-section__info-text_three{
  font-size: 1em;
  padding: 0 8px;
  color: white;
  background-color: #8a8784;
  position: relative;
}

.banner-section__info-text_three:before{
  content: '';
  width: 150px;
  height: 1px;
  background-color: white;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: -1
}
.banner-section__info-text_three:after{
  content: '';
  width: 200px;
  height: 1px;
  background-color: white;
  position: absolute;
  bottom: 25%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: -1;
}
@media screen and (max-width: 1290px) {
  .banner-section__info-block{
    width: calc(100vw / 4.98);
    height: calc(100vw / 4.98);
    font-size: calc(100vw / 64.5);
  }
  .banner-section__info-text_three{
    padding: 0 calc(100vw / 161.25);
  }
  .banner-section__info-text_three:before{
    width: calc(100vw / 8.6);
  }
  .banner-section__info-text_three:after{
    width: calc(100vw / 6.45);
  }
}
.banner-section__info-text__four{
  font-size: 1.5em;
  white-space: pre;
  margin: -1px 0 0 2px;
}
.banner-section__button{
  border: none;
  background-color: #f09d66;
  color: white;
  text-transform: uppercase;
  position: absolute;
  bottom: -46%;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 1.2em;
  padding: 0.55em 1.2em;
  white-space: nowrap;
}
@media screen and (max-width: 600px) {
  .banner-section__info-block{
    width: calc(125vw / 4.98);
    height: calc(125vw / 4.98);
    font-size: calc(125vw / 64.5);
  }
  .banner-section__info-text_three{
    padding: 0 calc(125vw / 161.25);
  }
  .banner-section__info-text_three:before{
    width: calc(125vw / 8.6);
  }
  .banner-section__info-text_three:after{
    width: calc(125vw / 6.45);
  }
  .banner-section__button{
    bottom: -50%;
  }
}
@media screen and (max-width: 360px) {
  .banner-section__info-block{
    width: 90px;
    height: 90px;
    font-size: 7px;
  }
  .banner-section__info-text_three{
    padding: 0 3px;
  }
  .banner-section__info-text_three:before{
    width: 52px;
  }
  .banner-section__info-text_three:after{
    width: 70px;
  }
  .banner-section__button{
    bottom: -50%;
  }
}
.slick-dots{
  position: absolute;
  bottom: 8%;
  display: flex;
  justify-content: center;
}
.slick-dots li{
  width: auto;
  height: auto;
  margin: 0 15px;
}
.slick-dots li button{
  width: auto;
  height: auto;
  padding: 0;
}
.slick-dots li button:before{
  content: '';
  background-color: #b2b2b2;
  width: 13px;
  height: 13px;
  transform: rotate(45deg);
  opacity: 0.7;
  transition: all .2s;
}
.slick-dots li.slick-active button:before{
  background-color: black;
  opacity: 1;
}
.slick-dots li button:hover:before, .slick-dots li.slick-active button:hover:before {
  border: 1px solid white;
}

/*Offers*/
.offers-section{
  position: relative;
  display: inline-block;
  width: 100%;
  margin-bottom: 129px;
  font-size: 30px;
  --border-width: 50px solid transparent;
  --border-height: 40px solid;
}
@media screen and (max-width: 1290px) {
  .offers-section{
    font-size: calc(100vw / 43);
    margin-bottom: calc(100vw / 10);
  }
}
.offers-section__item{
  height: 100px;
  color: white;
  padding: 30px 0 30px 30px;
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  background-color: var(--secondary-color);
}
.offers-section__item_left{
  left: 0;
  width: 30.4%;
}
.offers-section__item_center{
  left: 50%;
  transform: translate(-50%, 0);
  width: 35%;
  padding-left: 5%;
  background-color: black;
}
.offers-section__item_right{
  right: 0;
  width: 30.4%;
}
.offers-section__item_left::after,
.offers-section__item_center::after,
.offers-section__item_center::before,
.offers-section__item_right::after{
  content: '';
  display: inline-block;
  position: absolute;
  z-index: 1;
}
.offers-section__item_left::after{
  top: 5px;
  right: -70px;
  transform: rotate(90deg) translate(25px, 0);
  border-right: var(--border-width);
  border-bottom: var(--border-height) var(--secondary-color);
  border-left: var(--border-width);
}
.offers-section__item_center::after{
  top: 0;
  left: -1px;
  border-top: var(--border-width);
  border-bottom: var(--border-width);
  border-left: var(--border-height) white;
}
.offers-section__item_center::before{
  top: 0;
  right: -1px;
  transform: rotate(180deg);
  border-top: var(--border-width);
  border-bottom: var(--border-width);
  border-left: var(--border-height) white;
}
.offers-section__item_right::after{
  top: 0;
  left: -40px;
  transform: rotate(180deg);
  border-top: var(--border-width);
  border-bottom: var(--border-width);
  border-left: var(--border-height) var(--secondary-color);
}
.offers-section__item-icon{
  display: inline-block;
  width: 40px;
  height: 40px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  margin-right: 32px;
}
.offers-section__item-icon_center{
  margin-right: 20px;

}
@media screen and (max-width: 1290px) {
  .offers-section__item-icon{
    width: calc(100vw / 32.25);
    height: calc(100vw / 32.25);
    margin-right: calc(100vw / 59);
  }
  .offers-section__item-icon_center{
    margin-right: calc(100vw / 64.5);
  }
}
@media screen and (max-width: 900px) {
  .offers-section__item {
    height: 80px;
  }
  .offers-section{
    --border-height: 26px solid;
    --border-width: 40px solid transparent;
    margin-bottom: 80px;
  }
  .offers-section__item_left::after{
    top: 2px;
    right: -53px;
  }
  .offers-section__item_right::after{
    left: -26px;
  }
}
@media screen and (max-width: 750px) {
  .offers-section__item {
    height: 60px;
  }
  .offers-section{
    --border-height: 18px solid;
    --border-width: 30px solid transparent;
    margin-bottom: 60px;

  }
  .offers-section__item_left::after{
    top: -4px;
    right: -39px;
  }
  .offers-section__item_right::after{
    left: -18px;
  }
}
@media screen and (max-width: 600px) {
  .offers-section{
    display: block;
    margin: 6% 0;
    font-size: 18px;
  }
  .offers-section__item {
    margin: 2% 0;
    position: static;
    width: 100%;
    transform: none;
    padding: 30px 0 30px 25%;
  }
  .offers-section__item_left::after,
  .offers-section__item_center::after,
  .offers-section__item_center::before,
  .offers-section__item_right::after{
    display: none;
  }
  .offers-section__item-icon{
    width: 28px;
    height: 28px;
  }
}
.offers-section__main-text{
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: var(--lato);
  font-weight: bold;
  font-size: 1em;
  text-transform: uppercase;
}
.offers-section__second-text{
  font-size: 0.5em;
  margin-top: 0.5em;
  font-weight: normal;
  text-transform: none;
}

/*Title*/
.title-block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5% 0;
  --scale: 100vw;
}
.title-block:before,
.title-block:after{
  content: "";
  display: block;
  height: 1px;
  margin-right: 4.5%;
  background-color: #eaeaea;;
  flex-grow: 1;
}
.title-block:after{
  margin: 0 0 0 4.5%;
}
.title-block__title{
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: var(--playfair);
  font-style: italic;
  font-weight: 700;
  font-size: 30px;
  line-height: 30px;
}

.title-block__title::before, .title-block__title::after{
  content: '';
  display: inline-block;
  width: 14px;
  height: 14px;
  background-color: var(--secondary-color);
  transform: rotate(45deg);
  margin-right: 88px;
}
.title-block__title::after{
  margin: 0 0 0 88px;
}

@media screen and (max-width: 1290px) {
  .title-block__title{
    font-size: calc(var(--scale) / 43);
    line-height: calc(var(--scale) / 43);
  }

  .title-block__title::before, .title-block__title::after{

    width: calc(var(--scale) / 92);
    height: calc(var(--scale) / 92);
    margin-right: calc(100vw / 14.66);
  }
  .title-block__title::after{
    margin: 0 0 0 calc(var(--scale) / 14.66);
  }
}
@media screen and (max-width: 900px) {
  .title-block{
    --scale: 150vw;
  }
}
@media screen and (max-width: 600px) {
  .title-block{
    --scale: 200vw;
  }
}
@media screen and (max-width: 450px) {
  .title-block__title{
    font-size: 21px;
    line-height: 21px;
  }

  .title-block__title::before, .title-block__title::after{
    width: 10px;
    height: 10px;
    margin-right: 31px;
  }
  .title-block__title::after{
    margin: 0 0 0 31px;
  }
}


/*Featured*/
.featured-content{
  display: grid;
  grid-auto-flow: column;
  grid-template: 1fr 1fr / 400fr 460fr 400fr;
  grid-row-gap: 0.65em;
  grid-column-gap: 1em;
  margin-bottom: 5em;
  font-size: 20px;
}
.featured-content__item{
  padding-top: 100%;
  position: relative;
}
@media screen and (max-width: 1290px) {
  .featured-content {
    font-size: calc(100vw / 64.5);
  }
}
@media screen and (max-width: 600px) {
  .featured-content{
    display: block;
    font-size: calc(100vw / 18);
    margin-bottom: 0;
  }
  .featured-content__item:not(:last-child){
    margin-bottom: calc(100vw / 30.77);
  }
}
@media screen and (max-width: 360px) {
  .featured-content{
    font-size: 20px;
  }
  .featured-content__item:not(:last-child){
    margin-bottom: 12px;
  }
}
.featured-content__item_center{
  grid-row-start: span 2;
  padding-top: 178%;
}
.featured-content__item_center:after{
  content: '';
  display: inline-block;
  border: 0.15em solid white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 92%;
  height: 92%;
}

.featured-content__img{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
@media screen and (max-width: 600px) {
  .featured-content__item_center{
    padding-top: 100%;
    overflow: hidden;
  }
  .featured-content__img{
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
  }
}
.featured-content__item_with-button,
.featured-content__item_jewellery{
  position: relative;
}
.button{
  border: none;
  background-color: var(--secondary-color);
  color: white;
  text-transform: uppercase;
  font-family: var(--lato);
  font-size: 0.75em;
  font-weight: bold;
  padding: 1em;
  position: absolute;
  bottom: 16%;
  left: 50%;
  transform: translate(-50%, 0);
}
.jewellery-title-wrapper{
  position: absolute;
  top: 16%;
  width: 100%;
}
.jewellery-title-wrapper__title{
  text-transform: uppercase;
  font-family: var(--lato);
  font-weight: bold;
  font-size: 1.2em;
  margin: 0;
  text-align: center;
}
.jewellery-title-wrapper__title_second-title{
  margin-top: 0.93em;
  font-size: 0.7em;
}

.featured-content__second-img{
  display: none;
}

.center-title-wrapper{
  position: absolute;
  top: 35%;
  left: 12%;
}
.center-title-wrapper__title{
  font-family: var(--playfair);
  font-weight: bold;
  font-style: italic;
  font-size: 1.2em;
  margin: 0;
}
.center-title-wrapper__title_second-title{
  font-family: var(--lato);
  font-style: normal;
  text-transform: uppercase;
  color: white;
  margin-top: 0.56em;
  font-size: 1.8em;
}
.featured-content__item_discount{
  position: relative;
}
.discount-title-wrapper{
  position: absolute;
  top: 45%;
  transform: translate(-50%, -50%);
  left: 50%;
}
.discount-title-wrapper__title{
  font-family: var(--lato);
  font-weight: bold;
  font-size: 2.55em;
  margin: 0;
  padding-left: 0.078em;
}
.discount-title-wrapper__title_second-title {
  text-transform: uppercase;
  margin-top: -0.333em;
  font-size: 3em;
}


/*Collection*/
.collection-content{
  display: grid;
  grid-template: repeat(2, 1fr)/ repeat(4, 1fr);
  grid-gap: 0.75em 1em;
  padding: 1em 0.75em;
  border: 1px solid #eaeaea;
  align-items: start;
  margin-bottom: 5.3em;
  font-size: 20px;
}
@media screen and (max-width: 1290px) {
  .collection-content{
    font-size: calc(100vw / 64.5);
  }
}

@media screen and (max-width: 600px) {
  .collection-content{
    grid-template: repeat(4, 1fr)/ repeat(2, 1fr);
    font-size: calc(100vw / 30);
    margin-bottom: 0;
  }
}
@media screen and (max-width: 360px) {
  .collection-content{
    font-size: 12px;
  }
}
.collection-content__item{
  position: relative;
  padding-top: 116%;
}
.collection-content__item_label::after{
  content: attr(data-discount);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.73em;
  height: 2.73em;
  border-radius: 50%;
  background-color: #feab12;
  color: white;
  font-family: var(--lato);
  font-weight: bold;
  font-size: 1.1em;
  position: absolute;
  top: 5%;
  left: 5%;
}
.collection-content__img{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  transition: filter 0.4s;
}
.brightDown {
  filter: brightness(0.7);
}
.collection-content__add-button{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0);
  border: none;
  background-color: unset;
  border-bottom: 2.1em solid black;
  border-left: 2em solid transparent;
  border-right: 2em solid transparent;
  height: 0;
  width: 10em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.4s;
  font-size: 20px;
}

.collection-content__add-button::after{
  content: attr(data-text);
  display: inline-block;
  color: white;
  font-family: var(--lato);
  font-weight: bold;
  font-size: 0.7em;
  text-transform: uppercase;
  line-height: 3.1428em;
  z-index: 10;
  position: absolute;
  top: 0;
}
@media screen and (max-width: 1290px) {
  .collection-content__add-button{
    font-size: calc(100vw / 64.5);
  }
}
@media screen and (max-width: 800px) {
  .collection-content__add-button{
    font-size: calc(100vw / 50);
  }
}
@media screen and (max-width: 600px) {
  .collection-content__add-button{
    font-size: calc(100vw / 30);
  }
}
@media screen and (max-width: 360px) {
  .collection-content__add-button{
    font-size: 12px;
  }
}
.color-change{
  border-bottom-color: var(--secondary-color);
}

/*Brands*/

.brand-content{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 55px 0 80px;
}

.brand-content__img-wrapper{
  flex-basis: 15%;
  text-align: center;
}
.brand-content__img{
  max-width: 100%;
  height: auto;
}
@media screen and (max-width: 1290px) {
  .brand-content{
    padding: calc(100vw / 21.5) 0;
  }
}

@media screen and (max-width: 750px) {
  .brand-content{
    flex-wrap: wrap;
  }
  .brand-content__img-wrapper{
    flex-basis: 26%;
    margin: calc(100vw / 75) 0;
  }
}
@media screen and (max-width: 600px) {
  .brand-content{
    padding: 0;
  }
  .brand-content__img-wrapper{
    flex-basis: 34%;
  }
}

/*Says*/

.says-content {
  padding: 30px 0 4%;
  background-color: var(--secondary-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.says-content:before{
  content:'';
  display: block;
  position: absolute;
  top: -1px;
  z-index: 2;
  left: 50%;
  transform: translate(-50%, 0);
  width: 0;
  height: 0;
  border-left: 180px solid transparent;
  border-right: 180px solid transparent;
  border-top: 30px solid white;
}
.says-content__content-container{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  flex-basis: 50%;
  max-height: 320px;
  --margin-minus: -100px;
  font-size: 15px;
}
.says-content__img-wrapper{
  align-self: flex-start;
  width: 39%;
  margin: auto var(--margin-minus) auto 0;
  z-index: 0;
}
.says-content__img-wrapper_right-block{
  order: 1;
  align-self: flex-end;
  margin: auto 0 auto var(--margin-minus);
}
.says-content__img{
  width: 100%;
  height: auto;
}.says-content__img_right-block{

 }

.says-content__text{
  color: white;
  font-family: var(--playfair);
  font-size: 1.2em;
  font-style: italic;
  margin: 14.7% 0 0 0;
  white-space: pre-line;
  line-height: 1.85em;
  align-self: flex-start;
}
.says-content__text_right-block{
  text-align: right;
  align-self: flex-end;
}
.says-content__sayer-name{
  font-family: var(--playfair);
  font-size: 2em;
  font-style: italic;
  font-weight: bold;
  margin: 46px 0 0 0;
  align-self: start;
  z-index: 1;
}
.says-content__sayer-position{
  color: #7f7e7e ;
  font-family: var(--lato);
  font-size: 0.46em;
  font-weight: bold;
  display: block;
  margin-top: 19px;
}
.says-content__sayer-name_right-block{
  text-align: right;
  align-self: flex-end;
}
.says-content__sayer-position_right-block{
  text-align: right;
}
@media screen and (max-width: 1290px) {
  .says-content__content-container{
    max-height: calc(100vw / 4.03);
    font-size: calc(100vw / 86);
    --margin-minus: calc(100vw / -12.9);
  }
  .says-content {
    padding: 30px 0 4%;
  }
  .says-content::before{
    border-left: calc(100vw / 7.16) solid transparent;
    border-right: calc(100vw / 7.16) solid transparent;
    border-top: calc(100vw / 43) solid white;
  }
  .says-content__text{
    margin: calc(100vw / 14.33) 0 0 0;
  }
  .says-content__sayer-name{
    margin: calc(100vw / 28.04) 0 0 0;
  }
  .says-content__sayer-position{
    margin-top: calc(100vw / 67.9);
  }

}

@media screen and (max-width: 600px) {
  .says-content {
    display: block;
    padding: 30px 0;
  }
  .says-content::before{
    display: none;
  }
  .says-content__content-container{
    padding: 0 20px;
    max-height: calc(100vw / 1.85);
    --margin-minus: calc(100vw / -12);
    font-size: calc(100vw / 40);
  }
  .says-content__img-wrapper{
    width: 40%;
  }
  .says-content__text{
    margin: 10% 0 0 0;
  }
  .says-content__sayer-position{
    margin-top: calc(100vw / 24);
  }
}
@media screen and (max-width: 360px) {
  .says-content {
    display: block;
    padding: 30px 0;
  }
  .says-content::before{
    display: none;
  }
  .says-content__content-container{
    padding: 0 20px;
    max-height: 195px;
    --margin-minus: -30px;
    font-size: 9px;
  }
  .says-content__sayer-position{
    margin-top: 15px;
  }
}

/*Footer*/
.footer{
  position: relative;
  background-color: black;
  font-family: var(--lato);
  padding-bottom: 80px;
}
