  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
}

.upper-footer::before{
  content: '';
  height: 1px;
  background-color: #737373;
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: 10;
}
.upper-footer::after{
  content: '';
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
